import Vue from "vue";
import Router from "vue-router";
import Header from "./layout/Header";
import Portfolio from "./views/Portfolio.vue";

Vue.use(Router);

export default new Router({
  linkExactActiveClass: "active",
  mode: "history",
  routes: [
    {
      path: "/",
      name: "portfolio",
      components: {
        header: Header,
        default: Portfolio,
      }
    }
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});
