<template>
  <div>
      <modal :show="showModal" @close="closeModal">
          <h6 slot="header" class="modal-title" id="modal-title-default">{{ modalContent.longTitle }}</h6>
          <modal-content-wrapper :modalContent="modalContent"> </modal-content-wrapper>
      </modal>
    <full-page ref="fullpage" :options="options" id="fullpage"
    >
      <home />
      <about />
      <resume />
      <!-- <services />
      <clients /> -->
      <!-- <works @openModal="openModal"/> -->
      <!-- <blog /> -->
      <contact />
      
    </full-page>
  </div>
</template>

<script>
import Vue from "vue";

import Home from "./portfolio/Home.vue";
import About from "./portfolio/About.vue";
import Resume from "./portfolio/Resume.vue";
import Services from "./portfolio/Services.vue";
import Clients from "./portfolio/Clients.vue";
import Works from "./portfolio/Works.vue";
import Blog from "./portfolio/Blog.vue";
import Contact from "./portfolio/Contact.vue";
import Modal from "@/components/Modal.vue";
import ModalContentWrapper from "@/components/ModalContentWrapper.vue";

import VueFullPage from "vue-fullpage.js";
import "@/assets/scss/fullpage.css";
import "@/plugins/scrolloverflow.js";
import "fullpage.js/dist/fullpage.extensions.min.js";
import "@/plugins/fullpage.parallax.min.js";

Vue.use(VueFullPage);

export default {
  name: "portfolio",
  components: {
    Home,
    About,
    Resume,
    Services,
    Clients,
    Works,
    Blog,
    Contact,
    Modal,
    ModalContentWrapper,
  },
  data() {
    return {
      showModal: false,
      modalContent: Object,
      options: {
        navigation: true,
        navigationTooltips: ["Home", "About", "Resume", "Portofolio", "Contact"],
        anchors: ['home', 'about', 'resume'],
        // continuousVertical: true,
        parallax: true,
        scrollOverflow: true,
        scrollOverflowOptions: {
          preventDefault: false,
          disablePointer: true,
        },
        parallaxOptions: {
          type: "reveal",
          percentage: 100,
          property: "translate",
        },
        normalScrollElements: '.modal-open .modal',
        paddingTop: '85px',
      },
    };
  },
  methods: {
    openModal(payload) {
      this.showModal = true;
      this.modalContent = payload;
      fullpage_api.setAllowScrolling(false);
    },
    closeModal() {
      this.showModal = false;
      fullpage_api.setAllowScrolling(true);
    },
  },
  mounted() {
  },
};
</script>

